import './Nav.css'
import React from 'react'
import { Link } from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Accordion from 'react-bootstrap/Accordion'
import { Card, Button } from 'react-bootstrap'



export default props =>
    <aside className="menu-area">
        <nav className="menu">
            <Accordion>
                <Card>
                    <Card.Header>
                        <Link to="/" className="">
                            <i className="fa fa-home"></i> Início
                </Link><br />
                    </Card.Header>
                </Card>

                <Card>
                    <Card.Header>
                        <Accordion.Toggle as={Button} variant="" eventKey="0">
                        <FontAwesomeIcon icon="edit"/> Empresa <i className="fa fa-caret-down" />
                        </Accordion.Toggle>
                    </Card.Header>
                    <Accordion.Collapse eventKey="0">
                        <Card.Body>
                            <Link to="/empresa" className="">
                                 Empresa
                            </Link><br />
                            <Link to="/" className="">
                                 Planta Fixa
                            </Link><br />
                            <Link to="/" className="">
                                 Política Ambiental
                            </Link><br />
                            <Link to="/" className="">
                                 Responsabilidade Social
                            </Link>
                        </Card.Body>
                    </Accordion.Collapse>
                </Card>

                <Card>
                    <Card.Header>
                        <Accordion.Toggle as={Button} variant="" eventKey="1">
                        <FontAwesomeIcon icon="coffee" /> Serviços <i className="fa fa-caret-down" />
                        </Accordion.Toggle>
                    </Card.Header>
                    <Accordion.Collapse eventKey="1">
                        <Card.Body>
                            <Link to="/" className="">
                                 Serviços
                            </Link><br />
                            <Link to="/" className="">
                                 EcoSystem
                            </Link><br />
                            <Link to="/" className="">
                                 Análises
                            </Link><br />
                            <Link to="/" className="">
                                 Catálogo
                            </Link><br />
                        </Card.Body>
                    </Accordion.Collapse>
                </Card>

                <Card>
                    <Card.Header>
                        <Link to="/" className="">
                        <i className="fa fa-star"></i> Novidades
                        </Link><br />
                    </Card.Header>
                </Card>

                <Card>
                    <Card.Header>
                        <Link to="/" className="">
                        <i className="fa fa-phone"></i> Contato
                        </Link><br />
                    </Card.Header>
                </Card>


            </Accordion>

        </nav>
    </aside>